/* eslint-disable no-await-in-loop */
/* eslint-disable no-unused-vars */
/* eslint-disable no-return-assign */
/* eslint-disable import/no-webpack-loader-syntax */
import React, { useEffect, useRef, useState } from 'react'
import 'mapbox-gl/dist/mapbox-gl.css'
// eslint-disable-next-line import/no-unresolved
import mapboxgl from '!mapbox-gl'
import './FlightMap.css'

const FlightMap = ({ flights }) => {
  if (!flights[0]) {
    return <div>No Flights To Show</div>
  }
  const [currRoute, setRoute] = useState('')
  const [flightCount, setFlightCount] = useState('')
  mapboxgl.accessToken = 'pk.eyJ1IjoidG9tc21pdGg4MyIsImEiOiJjbDR3amg0dngwZnd2M3FxczAxdTJiYTNiIn0.MkEz6KZXSbiLcOBUv0dQZw'
  const mapContainer = useRef(null)
  const map = useRef(null)
  const [lng, setLng] = useState(122.56)
  const [lat, setLat] = useState(10.72)
  const [zoom, setZoom] = useState(7)

  useEffect(() => {
    const bucket = []
    flights.forEach((x) => {
      if (!bucket[x.route.id]) {
        bucket[x.route.id] = []
      }
      bucket[x.route.id].push(x)
    })

    const newBucket = bucket.filter((x) => x)
    const qwe = newBucket.map((x) => ({
      origin: x[0].route.origin.name,
      destination: x[0].route.destination.name,
      count: x.length,
    }))
    const newqwe = []
    const doStuff = async () => {
      for (let i = 0; i < qwe.length; i += 1) {
        const newOrg = qwe[i].origin.split(' ')
        let orgSearch = ''
        newOrg.forEach((y) => {
          orgSearch = `${orgSearch + y}%20`
        })
        const zxc = await (await (await fetch(`https://api.mapbox.com/geocoding/v5/mapbox.places/${orgSearch}.json?access_token=pk.eyJ1IjoidG9tc21pdGg4MyIsImEiOiJjbDR3amg0dngwZnd2M3FxczAxdTJiYTNiIn0.MkEz6KZXSbiLcOBUv0dQZw`)).json()).features[0].center

        const newDest = qwe[i].destination.split(' ')
        let destSearch = ''
        newDest.forEach((y) => {
          destSearch = `${destSearch + y}%20`
        })
        const cvb = await (await (await fetch(`https://api.mapbox.com/geocoding/v5/mapbox.places/${destSearch}.json?access_token=pk.eyJ1IjoidG9tc21pdGg4MyIsImEiOiJjbDR3amg0dngwZnd2M3FxczAxdTJiYTNiIn0.MkEz6KZXSbiLcOBUv0dQZw`)).json()).features[0].center
        newqwe.push({
          ...qwe[i], originLong: zxc[0], originLat: zxc[1], destLong: cvb[0], destLat: cvb[1],
        })
        console.log('here')
      }
      const features = []
      for (let i = 0; i < newqwe.length; i += 1) {
        features.push({
          type: 'Feature',
          properties: {
            count: newqwe[i].count,
            route: `${newqwe[i].origin} --- ${newqwe[i].destination}`,
          },
          geometry: {
            type: 'LineString',
            coordinates: [
              [
                newqwe[i].originLong,
                newqwe[i].originLat,
              ],
              [
                newqwe[i].destLong,
                newqwe[i].destLat,
              ],
            ],
          },
        })
      }
      console.log(features)
      if (map.current) return
      map.current = new mapboxgl.Map({
        container: mapContainer.current,
        style: 'mapbox://styles/mapbox/light-v10?optimize=true',
        center: [lng, lat],
        zoom,
      })
      if (!map.current) return
      map.current.on('move', () => {
        setLng(map.current.getCenter().lng.toFixed(4))
        setLat(map.current.getCenter().lat.toFixed(4))
        setZoom(map.current.getZoom().toFixed(2))
      })
      map.current.on('load', () => {
        map.current.addSource('flights', {
          type: 'geojson',
          data: {
            type: 'FeatureCollection',
            features,
          },
          generateId: true,
        })
        map.current.addLayer({
          id: 'flights',
          type: 'line',
          source: 'flights',
          layout: {
            'line-join': 'round',
            'line-cap': 'round',
          },
          paint: {
            'line-color':
            [
              'case',
              ['boolean', ['feature-state', 'hover'], false],
              '#888',
              '#888',
            ],
            'line-width': [
              'case',
              ['boolean', ['feature-state', 'hover'], false],
              ['+', 10, ['*', 0.5, ['get', 'count']]],
              ['+', 5, ['*', 0.5, ['get', 'count']]],
            ],
          },
        })
      })
      let quakeID = null
      map.current.on('mousemove', 'flights', (event) => {
        map.current.getCanvas().style.cursor = 'pointer'
        const { count } = event.features[0].properties
        const { route } = event.features[0].properties

        if (event.features.length === 0) return
        setFlightCount(count)
        setRoute(route)

        if (quakeID) {
          map.current.removeFeatureState({
            source: 'flights',
            id: quakeID,
          })
        }
        quakeID = event.features[0].id

        map.current.setFeatureState(
          {
            source: 'flights',
            id: quakeID,
          },
          {
            hover: true,
          },
        )
      })
      map.current.on('mouseleave', 'flights', () => {
        if (quakeID + 1) {
          map.current.setFeatureState(
            {
              source: 'flights',
              id: quakeID,
            },
            {
              hover: false,
            },
          )
        }
        quakeID = null
        setFlightCount('')
        setRoute('')
        map.current.getCanvas().style.cursor = ''
      })
    }
    doStuff()
  }, [flights])

  return (
    <div className="flightmap-container">
      <div ref={mapContainer} className="map-container" />
      {flightCount !== ''
        ? (
          <div className="infos">
            Route:
            {' '}
            {currRoute}
            <br />
            FlightCount:
            {' '}
            {flightCount}
          </div>
        )
        : null}
    </div>
  )
}

export default FlightMap
